.dashboard-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #454260;
  height: 100vh;
  width: 100vw;
}
.cards-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
a {
  display: -ms-flexbox;
  display: flex;
}
.card {
  height: 400px;
  width: 300px;
  margin: 10px;
  /* background: blue; */
}
.card1 {
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(121, 206, 137, 0.8)), to(rgb(121, 206, 137, 0.8))),
    url("../assets/application.jpg");
  background: -webkit-linear-gradient(rgb(121, 206, 137, 0.8), rgb(121, 206, 137, 0.8)),
    url("../assets/application.jpg");
  background: -o-linear-gradient(rgb(121, 206, 137, 0.8), rgb(121, 206, 137, 0.8)),
    url("../assets/application.jpg");
  background: linear-gradient(rgb(121, 206, 137, 0.8), rgb(121, 206, 137, 0.8)),
    url("../assets/application.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  /* background: rgb(121, 206, 137); */
}
.card2 {
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(141, 126, 220, 0.8)), to(rgb(141, 126, 220, 0.8))),
    url("../assets/interview.jpg");
  background: -webkit-linear-gradient(rgb(141, 126, 220, 0.8), rgb(141, 126, 220, 0.8)),
    url("../assets/interview.jpg");
  background: -o-linear-gradient(rgb(141, 126, 220, 0.8), rgb(141, 126, 220, 0.8)),
    url("../assets/interview.jpg");
  background: linear-gradient(rgb(141, 126, 220, 0.8), rgb(141, 126, 220, 0.8)),
    url("../assets/interview.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.card3 {
  background: -webkit-gradient( linear, left top, left bottom, from(rgb(107, 202, 197)), to(rgb(107, 202, 197))),
    url("../assets/company.jpg");
  background: -webkit-linear-gradient( rgb(107, 202, 197), rgb(107, 202, 197)),
    url("../assets/company.jpg");
  background: -o-linear-gradient( rgb(107, 202, 197), rgb(107, 202, 197)),
    url("../assets/company.jpg");
  background: linear-gradient( rgb(107, 202, 197), rgb(107, 202, 197)),
    url("../assets/company.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.card4 {
  background:-webkit-gradient(linear, left top, left bottom, from(rgb(233, 152, 147)), to(rgb(233, 152, 147))),
    url("../assets/connections.jpg");
  background:-webkit-linear-gradient(rgb(233, 152, 147), rgb(233, 152, 147)),
    url("../assets/connections.jpg");
  background:-o-linear-gradient(rgb(233, 152, 147), rgb(233, 152, 147)),
    url("../assets/connections.jpg");
  background:linear-gradient(rgb(233, 152, 147), rgb(233, 152, 147)),
    url("../assets/connections.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: black;
  width: 100%;
  height: 50px;
  background: white;
  margin: 120% 0 0 0;
}



